.rs-login-bg {
    background-image: url(/recommender-system/static/media/walmart.d3d6ef90.jfif);
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-filter: blur(8px);
            filter: blur(8px);
    height: 100vh;
}

.rs-login-c {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.rs-form-container {
    padding: 30px 50px;
    border-radius: 5px;
    background-color: rgb(255, 255, 255);
}

.rs-form {
    position: relative;
    width: 20em;
}

.rs-form-input {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    padding: 15px;
    border-radius: 4px;
    font-family: inherit;
    font-size: 18px;
    border: none;
    box-shadow: inset 0 0 0 1px #9d9fa2;
    background: none;
}

.rs-form-input:focus {
    outline-color: #505050;
}

.rs-form-label {
    position: relative;
    left: 15px;
    top: -35px;
    padding: 0 5px;
    color: #505050;
    cursor: text;
    transition: top 100ms ease-in,
        left 100ms ease-in,
        font-size 100ms ease-in;
}

.rs-form-input:not(:-ms-input-placeholder).rs-form-input:not(:focus) ~ .rs-form-label {
    top: -60px;
    left: 10px;
    font-size: 14px;
    background-color: white;
}

.rs-form-input:focus ~ .rs-form-label, .rs-form-input:not(:placeholder-shown).rs-form-input:not(:focus) ~ .rs-form-label {
    top: -60px;
    left: 10px;
    font-size: 14px;
    background-color: white;
}

.rs-login-button {
    width: 100%;
    padding: 12px;
    border-radius: 10px;
    border: none;
    font-size: 16px;
    font-weight: 600;
    color: white;
    background-color: #0065ff;
    box-shadow: inset 0 0 0 1px #0065ff;
    cursor: pointer;
}

.rs-login-button:hover {
    background-color: #0049fa;
}
